<template>
    <div></div>
</template>

<script>
import { getUserAgent } from "../../util";
export default {
    data() {
        return {
            qrcode_id:''
        };
    },
    mounted() {
        this.qrcode_id=this.$route.query.qrcode_id
        // 检测设备类型，判断是安卓还是ios直接拉起对应的应用
        getUserAgent()
            .then(android => {
                console.log(android);
                window.location.href = "qbaytek://www.qbaytek.com:80/customer?";
            })
            .catch(ios => {
                console.log(ios);
                window.location.href = "Qbay://?qrcode_id="+this.qrcode_id;
                console.log(this.qrcode_id)
            });
    },
    methods: {}
};
</script>

